import Layout from '@/module-dashboard/pages/layout'
const _import = require('@/router/import_' + process.env.NODE_ENV)

export default [

  {
    root: true,
    path: '/employees',
    component: Layout,
    name: 'employees',
    meta: {
      title: 'employees',
      icon: 'people'
    },
    children: [
      // 
      {
        path: 'index',
        component: _import('employees/pages/index'),
        name: 'employees-index',
        meta: {
          title: '',
          icon: 'people',
          noCache: true
        }
      },
      // 
      {
        path: 'details/:id',
        component: _import('employees/pages/details'),
        // hidden: true // 
        name: 'details',
        meta: {
          title: ''
        }
      },
      // 
      {
        path: 'report/:month',
        component: _import('employees/pages/report-list'),
        name: 'report',
        meta: {
          title: ''
        }
      },
      // 
      {
        path: 'archiving',
        component: _import('employees/pages/historical-archiving'),
        name: 'archiving',
        meta: {
          title: ''
        }
      },
      {
        path: 'print/:id',
        component: _import('employees/pages/print'),
        name: 'print',
        meta: {
          title: ''
        }
      },
      // 
      {
        path: 'leave/:id',
        component: _import('employees/pages/employees-leave'),
        // hidden: true // 
        name: 'leave',
        meta: {
          title: ''
        }
      },
      // 
      {
        path: 'adjustPost/:id',
        component: _import('employees/pages/employees-adjustpost'),
        // hidden: true, // 
        name: 'adjustPost',
        meta: {
          title: ''
        }
      },
      // 
      {
        path: 'import',
        component: _import('employees/pages/import'),
        // hidden: true, // 
        name: 'import',
        meta: {
          title: ''
        }
      }
    ]
  }
]



