import Layout from '@/module-dashboard/pages/layout'
const _import = require('@/router/import_' + process.env.NODE_ENV)

export default [
  {
    root: true,
    path: '/saas-clients',//
    component: Layout,
    redirect: 'noredirect',
    name: 'saas-clients',
    meta: {
      title: 'xxx',
      icon: 'component'
    },
    children: [
      {
        path: 'index',  //   -- /saas-cliens/index
        component: _import('saas-clients/pages/index'), //vue
        name: 'saas-clients-index',
        meta: {title: 'SAAS', icon: 'component', noCache: true}
      },
      {
        path: 'details/:id',
        component: _import('saas-clients/pages/detail'), //vue
        name: 'saas-clients-detail',
        meta: {title: 'SAAS', icon: 'component', noCache: true}
      }
    ]
  }
]



